.nav-icon {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition-duration: 1s;
  display: flex;
  position: relative;
}

.nav-icon span {
  height: 4px;
  width: 35px;
  background-color: #fff;
  border-radius: 20px;
  transition-duration: .25s;
  transition-delay: .25s;
  position: absolute;
}

.nav-icon span:before {
  height: 4px;
  width: 35px;
  content: "";
  background-color: #fff;
  border-radius: 20px;
  transition: transform .25s, top .25s .25s;
  position: absolute;
  top: -9px;
  left: 0;
}

.nav-icon span:after {
  height: 4px;
  width: 35px;
  content: "";
  background-color: #fff;
  border-radius: 20px;
  transition: transform .25s, top .25s .25s;
  position: absolute;
  top: 9px;
  left: 0;
}

.nav-icon.open span {
  background: none;
  transition-duration: .1s;
  transition-delay: .25s;
}

.nav-icon.open span:before {
  transition: top .25s, transform .25s .25s;
  top: 0;
  transform: rotateZ(-45deg);
}

.nav-icon.open span:after {
  transition: top .4s, transform .25s .25s;
  top: 0;
  transform: rotateZ(45deg);
}

